import React from 'react';
import Link from 'gatsby-link';
import {OutboundLink} from 'gatsby-plugin-google-analytics';
import phoneImage from '../images/phone.svg';
import logo from '../images/logo-dark.png';
import chatImage from '../images/chat.jpg';
import './index.css';

const IndexPage = () => (
  <div className="wrapper pt-5">
    <div className="container">
      <div className="row">
        <div className="col-sm-6">
          <img className="logo" src={logo} alt="" />
          <h3 className="mt-3 mb-4">A service integration telegram robot</h3>
          <p className="sub-title">Seery is a telegram robot make you can easily access different services, websites, in one command.</p>
          <p>Service lists:</p>
          <ul className="service-list mb-3">
            <li><a target="_blank" href="https://getpocket.com/">Pocket</a></li>
            <li><a target="_blank" href="https://wakatime.com">Wakatime</a></li>
            <li><a target="_blank" href="http://nba.com/">NBA</a></li>
            <li><a target="_blank" href="https://www.mlb.com/">MLB</a></li>
            <li><a target="_blank" href="https://news.ycombinator.com/">Hacker News</a></li>
            <li><a href="#">Short Url</a></li>
            <li style={{color: "#777"}}>To be added</li>
          </ul>
          <OutboundLink href="http://t.me/SeeryBot" className="btn btn-light">Start to automate your services</OutboundLink>
        </div>
        <div className="col-sm-6 xs-hide pr-5">
          <div className="phone-wrapper">
            <img src={phoneImage} alt="" className="phone" />
            <img src={chatImage} alt="" className="chat" />
          </div>
        </div>
      </div>
      <p className="own">Add your own services 😄 👉 <a href="https://github.com/ctxhou/seery">Github</a></p>
    </div>
  </div>
)

export default IndexPage



